@charset "UTF-8";

@import 'core';
#splashWrapper{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #ff00ff;
}

#splash{
    display: flex;
    flex-wrap: wrap;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 60px;
    height: 60px;
}
#splash p{
    background: #000;
    height: 20px;
    width: 20px;
    -webkit-animation: splashAnime 1.3s infinite ease-in-out;
    animation: splashAnime 1.3s infinite ease-in-out; 
}

#splash p:nth-child(1) {
    -webkit-animation-delay: 0.2s;
    animation-delay: 0.2s;
}
#splash p:nth-child(2) {
    -webkit-animation-delay: 0.3s;
    animation-delay: 0.3s;
}
#splash p:nth-child(3) {
    -webkit-animation-delay: 0.4s;
    animation-delay: 0.4s;
}
#splash p:nth-child(4) {
    -webkit-animation-delay: 0.1s;
    animation-delay: 0.1s;
}
#splash p:nth-child(5) {
    -webkit-animation-delay: 0.2s;
    animation-delay: 0.2s;
}
#splash p:nth-child(6) {
    -webkit-animation-delay: 0.3s;
    animation-delay: 0.3s;
}
#splash p:nth-child(7) {
    -webkit-animation-delay: 0s;
    animation-delay: 0s;
}
#splash p:nth-child(8) {
    -webkit-animation-delay: 0.1s;
    animation-delay: 0.1s;
}
#splash p:nth-child(9) {
    -webkit-animation-delay: 0.2s;
    animation-delay: 0.2s;
}

@-webkit-keyframes splashAnime {
    0%, 70%, 100% {
        -webkit-transform: scale3D(1, 1, 1);
        transform: scale3D(1, 1, 1);
    } 35% {
        -webkit-transform: scale3D(0, 0, 1);
        transform: scale3D(0, 0, 1); 
    }
}

@keyframes splashAnime {
    0%, 70%, 100% {
        -webkit-transform: scale3D(1, 1, 1);
        transform: scale3D(1, 1, 1);
    } 35% {
        -webkit-transform: scale3D(0, 0, 1);
        transform: scale3D(0, 0, 1);
    } 
}